@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap);
.sideBar {
  background-color: #121212;
  display: flex;
  color: #fff;
  flex-direction: column;
  justify-content: space-between;
  padding: 35px 0;
  align-items: center;
  width: 150px;
  grid-gap: 25px;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  bottom: 0;
}

.image {
  width: 75px;
}

.premiumImage {
  width: 80%;
  margin-bottom: 25px;
}

.list {
  width: 100%;
  list-style-type: none;
  display: flex;
  flex-direction: column;
}

.marginTop {
  margin-top: 45px;
}

.link {
  padding: 10px 0 10px 25px;
  border-left: 5px solid transparent;
  color: #fff;
  font-size: 15px;
  text-decoration: none;
  width: 100%;
  transition: 0.5s;
}

.link:hover {
  background-color: #6c34fb56;
}

.prem {
  text-align: center;
}

.button {
  background-color: #6e34fb;
  width: -webkit-max-content;
  width: -moz-max-content;
  width: max-content;
  padding: 7px 15px;
  color: #fff;
  font-weight: bold;
  margin: 15px auto 0 auto;
  border-radius: 5px;
  cursor: pointer;
}

.button:hover {
  background-color: hsla(257, 96%, 59%, 0.9);
}

.active {
  border-left: 5px solid #6e34fb !important;
  font-weight: bold;
  background-color: #6c34fb56;
}

.heroSection {
  height: 500px;
  display: flex;
  justify-content: space-around;
  margin: 50px 35px;
  border-radius: 25px;
  box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
  background-image: linear-gradient(rgba(0, 0, 0, 0.4), rgba(0, 0, 0, 0.4)),
    url(/Job/static/media/abstract.e3c483d6.jpg);
  background-position: center;
  background-size: cover;
  color: #fff;
  padding: 0 25px;
}

@media (min-width: 700px) {
  .heroSection {
    align-items: center;
  }
}

@media (max-width: 1020px) {
  .heroSection {
    margin-top: 25px;
    margin-left: 15px;
    margin-right: 15px;
  }
}

@media (max-width: 700px) {
  .heroSection {
    display: grid;
    grid-template-rows: -webkit-max-content 1fr;
    grid-template-rows: max-content 1fr;
    margin-left: 0px;
    margin-right: 0px;
  }
}

.hero-img-div {
  height: 100%;
  width: -webkit-max-content;
  width: -moz-max-content;
  width: max-content;
}

@media (max-width: 700px) {
  .hero-img-div {
    grid-row: span 2;
  }
}

.imageStyle {
  height: 100%;
  /* width: 100%; */
  object-fit: contain;
  padding-top: 15px;
}

.title {
  font-size: 75px;
}

@media (max-width: 1150px) {
  .title {
    font-size: 50px;
  }

  h2 {
    font-size: 20px;
  }
}

.buttonArea {
  display: flex;
}

.primaryButton {
  background-color: #6e34fb;
  font-weight: bold;
  border-radius: 10px;
  padding: 10px 30px;
  margin-right: 15px;
  margin-top: 15px;
  border: 2px solid #6e34fb;
  cursor: pointer;
}

.primaryButton:hover {
  background-color: hsla(257, 96%, 59%, 0.8);
}

.secondaryButton {
  margin-top: 15px;
  font-weight: bold;
  border-radius: 10px;
  padding: 10px 30px;
  border: 2px solid #6e34fb;
  cursor: pointer;
}

.secondaryButton:hover {
  background-color: hsla(257, 96%, 59%, 0.3);
}

a {
  color: #fff;
  text-decoration: none;
}

.tag-box {
  background-color: #fff;
  box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
  border-radius: 25px;
  width: 100%;
  padding: 25px 25px;
}

.tag-list {
  margin-top: 10px;
  display: flex;
  flex-wrap: wrap;
}

.tag {
  margin-top: 15px;
  margin-right: 8px;
  background-color: #f4f5f9;
  padding: 5px 10px;
  border-radius: 5px;
  box-shadow: rgba(0, 0, 0, 0.05) 0px 6px 24px 0px,
    rgba(0, 0, 0, 0.08) 0px 0px 0px 1px;
  transition: 0.5s;
}

@media (max-width: 1550px) {
  .tag {
    font-size: 12px;
  }
}

@media (max-width: 1300px) {
  .tag {
    font-size: 10px;
    margin-top: 10px;
    margin-right: 5px;
    padding: 3px 7px;
  }
}

.tag:hover {
  background-color: #d3d3d3;
}

.grid2 {
  display: grid;
  grid-template-columns: 1fr 2fr;
  grid-gap: 35px;
  padding: 0px 35px;
}

.grid3 {
  display: grid;
  grid-template-columns: 1.4fr 1fr 1fr;
  grid-gap: 35px;
  padding: 0px 35px;
}

@media (max-width: 1450px) {
  .grid2,
  .grid3 {
    grid-gap: 15px;
  }
}

.companies-box {
  background-color: #fff;
  box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
  border-radius: 25px;
  width: 100%;
  padding: 25px 25px;
  height: 100%;
  display: grid;
  grid-template-rows: -webkit-max-content 1fr;
  grid-template-rows: max-content 1fr;
  grid-gap: 25px;
}

.companies-box-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.image-grid {
  height: 100%;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-gap: 20px;
  grid-template-rows: 1fr;
  overflow: hidden;
}

@media (max-width: 130px) {
  .image-grid {
    grid-template-columns: 1fr 1fr;
  }
}

.img {
  overflow: hidden;
  height: 100%;
  width: 100%;
  border-radius: 15px;
  position: relative;
  cursor: pointer;
  grid-row: span 2;
}

.img > img {
  object-fit: cover;
  width: 100%;
  height: 100%;
  transition: transform 0.5s ease;
}

.img > img:hover {
  transform: scale(1.5);
}

.companies-name {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  pointer-events: none;
  background: linear-gradient(#e6646400, rgba(0, 0, 0, 0.5));
  display: flex;
  font-weight: bold;
  align-items: flex-end;
  padding: 15px;
  color: #fff;
  z-index: 10;
}

.job-counter {
  background-color: #fff;
  box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
  border-radius: 25px;
  margin-top: 50px;
  width: 100%;
  padding: 25px 25px;
  background-image: linear-gradient(rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.3)),
    url(/Job/static/media/available.3e822c2c.jpg);
  background-size: cover;
  background-position: top;
  color: #fff;
  margin-bottom: 50px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

@media (max-width: 1200px) {
  .job-counter {
    margin-top: 25px;
  }
}

.counter {
  font-size: 100px;
  font-weight: bold;
  margin-top: 25px;
  border-left: 5px solid #f99560;
  padding-left: 15px;
}

.job-counter > .primaryButton {
  background-color: #f99560;
  width: -webkit-max-content;
  width: -moz-max-content;
  width: max-content;
  border: 2px solid #f99560;
}

.job-counter > .primaryButton:hover {
  background-color: hsla(21, 93%, 68%, 0.5);
}

.resume-box {
  background-color: #fff;
  box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
  border-radius: 25px;
  margin-top: 50px;
  width: 100%;
  padding: 25px 25px;
  background-size: cover;
  background-position: center;
  margin-bottom: 50px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

@media (max-width: 1200px) {
  .resume-box {
    margin-top: 25px;
  }
}

.resume-text {
  margin-top: 25px;
  line-height: 25px;
}

@media (max-width: 1200px) {
  .resume-text {
    /* margin-top: 5px; */
  }
}

.resume-box > .primaryButton {
  color: #fff;
  background-color: #f99560;
  width: -webkit-max-content;
  width: -moz-max-content;
  width: max-content;
  border: 2px solid #f99560;
}

.resume-box > .primaryButton:hover {
  background-color: hsla(21, 93%, 68%, 0.5);
}

.blog-box {
  background-color: #f99560;
  box-shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
  border-radius: 25px;
  margin-top: 50px;
  color: #fff;
  width: 100%;
  padding: 25px 25px;
  background-size: cover;
  background-position: center;
  margin-bottom: 50px;
  display: grid;
  grid-template-rows: -webkit-max-content 1fr;
  grid-template-rows: max-content 1fr;
  grid-gap: 25px;
}

@media (max-width: 1200px) {
  .blog-box {
    margin-top: 25px;
  }
}

.blog-flex {
  display: grid;
  grid-template-columns: -webkit-max-content 1fr;
  grid-template-columns: max-content 1fr;
}

.blog-flex > img {
  object-fit: contain;
  width: 100%;
  height: 100%;
  object-position: 100% 100%;
}

.blog-icon {
  cursor: pointer;
  background-color: #fff;
  width: -webkit-max-content;
  width: -moz-max-content;
  width: max-content;
  border-radius: 5px;
  padding: 5px;
  height: -webkit-max-content;
  height: -moz-max-content;
  height: max-content;
  align-self: flex-end;
  transform-origin: left bottom;
  transition: 1s;
  border: 2px solid #fff;
}

.blog-icon:hover {
  transform: scale(1.3);
  background-color: rgba(255, 255, 255, 0.6);
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Roboto", sans-serif;
}

input:focus {
  outline: none;
  border-radius: 5px;
  border: 2px solid #6e34fb !important;
}

input[type="radio"]:checked + label > .selector {
  background-color: #6e34fb !important;
}

input[type="radio"]:checked + label > .job {
  background: linear-gradient(#7032ff, #6e34fb) !important;
  color: #fff !important;
}

.App {
  display: grid;
  grid-template-columns: -webkit-max-content 1fr;
  grid-template-columns: max-content 1fr;
  background-color: #f4f5f9;
  color: #383a3b;
}

